  display: flex;
}

.nav-list {
  list-style-type: none;
  display: flex;
}

.nav-item {
  text-transform: uppercase;
  line-height: 60px;
  position: relative;
}

.nav-item:not(:last-child) {
  margin-right: 20px;
}

.nav-link.is-active {
  color: var(--main-color);
}

.nav-link.is-active:after {
  content: "";
  background-color: var(--main-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
